/* Globales
----------------------------------------------------------*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  background-color: #000000;
}

a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7019607843);
  font-family: "Poppins", sans-serif;
  letter-spacing: 1px;
  font-weight: 500;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
  width: auto;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  color: #FFFFFF;
}

/* Tipografia
----------------------------------------------------------*/
h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2rem 0;
  line-height: 1.2;
  color: #FFFFFF;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 2.5rem;
  margin-bottom: 4rem;
}

h3 {
  font-size: 2rem;
}

p, label {
  color: rgba(255, 255, 255, 0.7019607843);
  font-size: 1.6rem;
  line-height: 2;
  font-family: "Inter", sans-serif;
  font-weight: 300;
  letter-spacing: 1px;
}

/* Utilidades
----------------------------------------------------------*/
.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .contenedor {
    width: 70%;
  }
}

.seccion {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .seccion {
    padding: 18rem 0 0 0;
  }
}

.degradado-verde {
  color: transparent; /* necesario  definir un color transparente al texto*/
  background: linear-gradient(to right, #2ceded 0%, #1073BA 100%);
  -webkit-background-clip: text; /* prefijo necesario en la mayoria de navegadores */
  background-clip: text; /*permite aplicar el degradado en el texto */
}

.bg-n1 {
  background-color: #111111;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: min(128rem, 90%);
  margin: 0 auto;
  padding-top: 2rem;
}
.header__logo {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.header__nombre {
  font-weight: 700;
  font-size: 2.5rem;
  line-height: unset;
  z-index: 1;
}
.header__imgLogo {
  width: 4rem;
}
.header__nav {
  display: flex;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .header__nav {
    gap: 4rem;
  }
}

.menu__enlace {
  z-index: 1;
  position: relative;
}
@media (min-width: 768px) {
  .menu__enlace {
    display: none;
  }
}
.menu__icon {
  width: 2.5rem;
}
.menu__nav {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #000000;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: all 0.15s cubic-bezier(0.86, 0, 0.07, 1);
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .menu__nav {
    position: unset;
    background-color: unset;
    display: flex;
    flex-direction: row;
    height: auto;
    transition: unset;
  }
}
.menu__link {
  padding: 2.2rem 0;
}
@media (min-width: 768px) {
  .menu__link {
    margin-left: 3rem;
    margin-right: 0.5rem;
    padding: 0;
  }
  .menu__link:last-of-type {
    font-weight: 700;
    margin-right: 0;
  }
}
@media (min-width: 1024px) {
  .menu__link {
    margin-right: 2.5rem;
  }
}
.menu .menu__nav--visible {
  height: 43rem;
  padding-top: 4rem;
  box-shadow: 0px -6px 21px 0px rgb(255, 255, 255);
}
@media (min-width: 768px) {
  .menu .menu__nav--visible {
    height: auto;
    padding-top: 0;
    border-bottom: unset;
  }
}

/* perfil
----------------------------------------------------------*/
.perfil {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .perfil {
    flex-direction: row;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .perfil {
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .perfil__info {
    flex: 2;
  }
}
@media (min-width: 1024px) {
  .perfil__info {
    flex: 4;
  }
}
.perfil__intro {
  font-size: 3.5rem;
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-weight: 300;
}
@media (min-width: 768px) {
  .perfil__intro {
    text-align: left;
    font-size: 3.7rem;
  }
}
@media (min-width: 1024px) {
  .perfil__intro {
    font-size: 4rem;
  }
}
.perfil__descripcion {
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.7019607843);
  font-weight: 300;
}
.perfil__nav {
  display: flex;
  gap: 4rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .perfil__nav {
    justify-content: unset;
    gap: 3rem;
  }
}
.perfil__link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
@media (min-width: 1024px) {
  .perfil__link {
    gap: 1rem;
  }
}
.perfil__nombre {
  font-size: 4vw;
}
@media (min-width: 480px) {
  .perfil__nombre {
    font-size: 1.6rem;
  }
}
.perfil__arrow {
  width: 4vw;
  height: 4vw;
}
@media (min-width: 480px) {
  .perfil__arrow {
    width: 2rem;
    height: 2rem;
  }
}
.perfil__fotoContainer {
  flex: 1;
  order: -1;
  margin: 0 auto 3rem auto;
  max-width: 22rem;
}
@media (min-width: 768px) {
  .perfil__fotoContainer {
    order: 0;
    margin: unset;
    max-width: unset;
  }
}
.perfil__foto {
  object-fit: cover;
  border-radius: 50%;
  min-width: 22rem;
  height: 22rem;
}

/* sobre mi
----------------------------------------------------------*/
.sobreMi__contenido {
  padding: 5rem 0;
  border-bottom: 1px solid #ffffff;
}
.sobreMi__secciones {
  position: relative;
}
.sobreMi__input {
  left: -9999px;
  position: absolute;
}
.sobreMi__input + label {
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.3215686275);
  display: block;
}
@media (min-width: 768px) {
  .sobreMi__input + label {
    width: 20%;
    border: none;
    border-left: 2px solid rgba(255, 255, 255, 0.3215686275);
  }
}
.sobreMi__input:checked + label {
  background-color: #1f1e1e;
  color: #ffffff;
  border: 2px solid #ffffff;
  display: block;
}
@media (min-width: 768px) {
  .sobreMi__input:checked + label {
    border: none;
    border-left: 2px solid #FFFFFF;
  }
}
.sobreMi__input:checked ~ .sobreMi__info {
  display: block;
  height: auto;
  opacity: 1;
  transition: opacity 0.4s cubic-bezier(0.55, 0.085, 0.68, 0.53);
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .sobreMi__input:checked ~ .sobreMi__info {
    padding: 0;
  }
}
.sobreMi__nombre {
  cursor: pointer;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 300;
  margin: 0;
  transition: transform 0.3s ease;
  height: 100%;
  padding: 1.2rem 0 1.2rem 1rem;
}
.sobreMi__nombre:hover {
  transform: translateX(5px);
}
@media (min-width: 768px) {
  .sobreMi__nombre {
    padding-left: 1.5rem;
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  .sobreMi__nombre {
    font-size: 2rem;
  }
}
.sobreMi__info {
  opacity: 0;
  height: 0;
  overflow: hidden;
}
@media (min-width: 768px) {
  .sobreMi__info {
    position: absolute;
    top: 0;
    left: 25%;
    width: 80%;
  }
}
.sobreMi__title {
  margin: 0;
}
.sobreMi__sitio {
  color: #6cace4;
  opacity: 0.8;
  font-weight: 300;
  font-size: 1.4rem;
}
.sobreMi__item {
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.7019607843);
  font-weight: 300;
  list-style-position: inside;
}

/* Skills
----------------------------------------------------------*/
.skills {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .skills {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
}
.skills__heading {
  text-align: center;
  padding-bottom: 2rem;
}
.skills__skill {
  padding: 1.5rem;
  height: auto;
  position: relative;
}
.skills__contenido {
  width: 100%;
  text-align: center;
}
.skills__nombre {
  margin: 0.5rem 0 0 0;
}
.skills__imagen {
  width: 2.8rem;
  margin: 0 auto;
}

/* hobbies
----------------------------------------------------------*/
.hobbies {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 2rem;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .hobbies {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
}
.hobbies__heading {
  text-align: center;
  padding-bottom: 2rem;
}
.hobbies__hobbie {
  padding: 1.5rem;
  height: auto;
  position: relative;
}
.hobbies__contenido {
  width: 100%;
  text-align: center;
}
.hobbies__nombre {
  margin: 0.5rem 0 0 0;
}
.hobbies__imagen {
  margin: 0 auto;
}

/* formacion
----------------------------------------------------------*/
.formacion {
  width: 80%;
  margin: 4rem auto 0 auto;
}
.formacion__heading {
  text-align: center;
}
@media (min-width: 480px) {
  .formacion__contenido {
    display: flex;
    justify-content: center;
  }
}
.formacion__elemento {
  margin-bottom: 2rem;
}
.formacion__elemento:last-of-type {
  margin: 0;
}
@media (min-width: 480px) {
  .formacion__elemento {
    margin-bottom: 0;
    width: 35rem;
    margin-right: 3rem;
  }
  .formacion__elemento:last-of-type {
    margin: 0;
  }
}
.formacion__imagenContainer {
  display: flex;
  justify-content: center;
}
.formacion__imagen {
  height: 10rem;
  width: 10rem;
  object-fit: contain;
}
@media (min-width: 768px) {
  .formacion__imagen {
    height: 12rem;
    width: 12rem;
  }
}
.formacion__info {
  text-align: center;
}
.formacion__titulo {
  font-size: 1.5rem;
  margin: 1.5rem 0 0 0;
}
.formacion__texto {
  margin: 0;
}

.experiencia___grid {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .experiencia___grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    row-gap: 2rem;
  }
}
.experiencia__elemento {
  overflow: hidden;
}
.experiencia__imagen {
  border-radius: 3px;
  width: 100%;
  object-fit: cover;
  min-height: 20rem;
  border-inline: 2px solid rgba(255, 255, 255, 0.7215686275);
}
.experiencia__info {
  padding: 2rem 0;
}
.experiencia__header {
  display: flex;
  justify-content: space-between;
}
.experiencia__titulo {
  color: #FFFFFF;
  margin: 0 0 1.4rem 0;
  font-size: 2rem;
  letter-spacing: 1px;
  font-weight: 700;
}
.experiencia__icon {
  font-size: 2rem;
}
.experiencia__descripcion {
  margin: 0;
  line-height: 1.6;
  font-size: 1.3rem;
}

.contacto {
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .contacto {
    padding: 18rem 0;
  }
}
.contacto__contenido {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 0;
  row-gap: 7rem;
}
@media (min-width: 768px) {
  .contacto__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 8rem;
    row-gap: 0;
  }
}
@media (min-width: 1024px) {
  .contacto__contenido {
    grid-template-columns: 1.5fr 2fr;
  }
}
.contacto__h3 {
  text-align: center;
  font-weight: 500;
  margin-bottom: 3rem;
  text-transform: uppercase;
  font-size: 1.7rem;
}
@media (min-width: 768px) {
  .contacto__h3 {
    font-size: 1.5rem;
  }
}
@media (min-width: 1024px) {
  .contacto__h3 {
    font-size: 1.7rem;
  }
}
.contacto__h3--formulario {
  margin-bottom: 4rem;
}
.contacto__medios {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  column-gap: 2rem;
  row-gap: 2rem;
}
.contacto__medio {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background-color: rgb(13, 13, 13);
  border-radius: 10px;
}
.contacto__icon {
  width: 4rem;
  margin: 0 auto 1rem auto;
}
.contacto__nombre {
  margin: 0;
  font-weight: 900;
  line-height: 1.2;
}
.contacto__referencia {
  margin: 0 0 1.5rem 0;
}
.contacto__enlace {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
}
.contacto__enlace:hover .contacto__arrow {
  transform: rotate(180deg) translate(-4px, 1px);
}
.contacto__arrow {
  transition: all 0.3s;
  font-size: 1.4rem;
  transform: rotate(180deg) translateY(1px);
  height: 100%;
}

.formulario__seccion {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
  height: 6rem;
  position: relative;
}
.formulario__seccion:last-of-type {
  height: 25rem;
  margin-bottom: 0;
}
.formulario__label {
  position: absolute;
  top: -17px;
  left: 20px;
  background-color: #000000;
  z-index: 1;
  padding: 0 0.5rem;
}
.formulario__input {
  position: absolute;
}
.formulario__input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px black inset;
  -webkit-text-fill-color: white;
}
.formulario__input, .formulario__textarea {
  padding-inline: 2.5rem;
  width: 100%;
  height: 100%;
  background-color: transparent;
  color: #FFFFFF;
  border-radius: 10px;
  outline: none;
  border: 1px solid #FFFFFF;
}
.formulario__textarea {
  padding-top: 2rem;
}
.formulario__submit {
  color: #000000;
  background-color: #FFFFFF;
  border: 1px solid #000000;
  padding: 1rem 3rem;
  font-family: "Inter", sans-serif;
  margin-top: 4rem;
  font-weight: 700;
  width: 100%;
  cursor: pointer;
  border-radius: 5px;
}